@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;400;500;800&display=swap');
*{
  margin:0;
  padding: 0;
  /* box-sizing: border-box; */
}
body{
  font-family: 'Manrope', sans-serif;
}
.stickey__header{
  width: 100%;
  height: 80px;
 line-height: 80px;
 position: sticky;
top: 0;
left: 0;
z-index: 99999;
box-shadow: 3px 3px -8px 3px #ddd;
background-color: #fff;
}

@media only screen and  (max-width:768px ){
.navigation{
  width: 100%;
  height: 100%;
  position: fixed;
top: 0;
left: 0;
z-index: 100;
background: #00000084;
display: none;
}
.menu{
  width: 15rem;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background:#fff;
  z-index: 200;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}
.show__menu{
  display: block;

}



}
  
